/* message */
#message {
	width: 100%;
	height: 100%;
}
#message .mint-header{
	width: 100%;
	height: 50px;
	background-color: white;
	color: black;
	border-bottom: 1px solid #b2b2b2;
	position: fixed;
    top: 0;
}
#message h1{
	margin-top: 7px;
	font-size: 16px;
}
#message h3 {
	font-size: 10px;
    padding-top: 5px;
}
#message h5 {
	font-size: 13px;
	padding-bottom: 5px;
}
#message .mint-button-text {
	font-size: 16px;
}
#message .mint-header-button {
	color: #0087e0;
}
/* container */
#message .container {
    padding: 10px;
    position: absolute;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    bottom: 50px;
    overflow: auto;
}
#message .container li {
	list-style: none;
	margin-bottom: 16px;
	width: 100%;
}
#message .container li:last-child {
	margin-bottom: 0;
}
#message li {
	list-style: none;
}
#message .container img {
	border-radius: 4px;
	width: 38px;
}
#message .container .other .msg {
	padding: 10px 15px;
	font-size: 14px;
	background-color: #eeeeee;
	border-radius: 5px;
	max-width: 170px;
	/*max-height: 150px;*/
	position: relative;
	margin-left: 15px;
	margin-top: 10px;
	word-break: break-all;		/* ! */
}
#message .mine .msg {
	max-width: 160px;
    font-size: 14px;
    line-height: 20px;
    background-color: #0088cf;
    border-radius: 5px;
    padding: 10px 25px 10px 15px;
    margin-left: 10px;
    margin-right: 23px;
    position: relative;
    word-break: break-all;		/* ! */
    color: white;
}
#message .tri {
	width: 0; 
	height: 0; 
	border-top: 10px solid #eeeeee;
	position: absolute;
}
#message .other .tri {
	border-left: 15px solid transparent;
	left: -10px;
    top: 0;
}
#message .mine .tri {
	border-right: 15px solid transparent;
	right: -10px;
    top: 0;
    border-top-color: #0088cf;
}
#message .mint-actionsheet-listitem, .mint-actionsheet-button {
	border-radius: 5px;
	height: 44px;
	/*background-color: rgba(255,255,255,.2);*/
    line-height: 44px;
    margin-top: 0;
}
/* footer */
#message footer>div {
	width: 100%;
	/*height: 37px;*/
	border-top: 1px solid #eeeeee;
	position: fixed;
	bottom: 0;
	z-index: 100;
    background-color: white;
}
#message footer img:first-child {
    width: 14px;
    position: absolute;
    bottom: 10px;
    left: 10px;
}
#message footer img:last-child {
    width: 23px;
    position: absolute;
    bottom: 12px;
    right: 10px;
}
#message footer #msg {
	float: left;
	width: 77%;
	max-height: 50px;
    font-size: 14px;
    line-height: 20px;
    border: 0;
    border-bottom: 1px solid #dedede;
    vertical-align: super;
    overflow: auto;
    padding: 0 0 5px 8px;
    margin: 13px 0 5px 26px;
    word-break: break-all;
}
#message footer #msg:focus {
	outline: 0;
}












































































